import { useEffect, useCallback, useRef } from 'react';
import { useFeed } from '../../hooks/useFeed';
import { COLUMN_TYPES } from '../../utils/constants';
import Post from '../posts/Post';
import PostComposer from '../posts/PostComposer';
import Loading from '../common/Loading';

function FeedColumn({ column }) {
  const { feed, isLoading, error, hasMore, refresh, loadMore } = useFeed(column.id);
  const containerRef = useRef(null);

  // Show composer only for home feed
  const showComposer = column.type === COLUMN_TYPES.HOME;

  // Infinite scroll
  const handleScroll = useCallback(() => {
    if (!containerRef.current || isLoading || !hasMore) return;

    const { scrollTop, scrollHeight, clientHeight } = containerRef.current;
    if (scrollHeight - scrollTop <= clientHeight * 1.5) {
      loadMore();
    }
  }, [isLoading, hasMore, loadMore]);

  useEffect(() => {
    const container = containerRef.current;
    if (container) {
      container.addEventListener('scroll', handleScroll);
      return () => container.removeEventListener('scroll', handleScroll);
    }
  }, [handleScroll]);

  return (
    <div
      ref={containerRef}
      className="flex-1 overflow-y-auto scrollbar-hide"
    >
      {/* Composer */}
      {showComposer && (
        <PostComposer onPost={refresh} />
      )}

      {/* Loading state */}
      {isLoading && feed.length === 0 && (
        <div className="flex items-center justify-center py-8">
          <Loading size="lg" />
        </div>
      )}

      {/* Error state */}
      {error && feed.length === 0 && (
        <div className="flex items-center justify-center py-8 text-text-muted">
          <p>Failed to load feed</p>
        </div>
      )}

      {/* Feed items */}
      {feed.map((item, index) => (
        <Post
          key={item.post?.uri || item.uri || index}
          item={item}
        />
      ))}

      {/* Load more indicator */}
      {isLoading && feed.length > 0 && (
        <div className="flex items-center justify-center py-4">
          <Loading size="md" />
        </div>
      )}

      {/* End of feed */}
      {!hasMore && feed.length > 0 && (
        <div className="text-center py-8 text-text-muted text-sm">
          You've reached the end
        </div>
      )}

      {/* Empty state */}
      {!isLoading && feed.length === 0 && !error && (
        <div className="flex items-center justify-center py-8 text-text-muted">
          <p>No posts yet</p>
        </div>
      )}
    </div>
  );
}

export default FeedColumn;
